export default {
	data() {
		return {
			value: "",
			hearlyWeather: []
		}
	},
	methods: {
		onChange(e) {
			this.value = e.detail;
		},
		onSearch() {
			let that = this
			uni.request({
				url: "https://api.heweather.net/s6/weather/hourly",
				data: {
					location: this.value,
					key: "f6185842beea4a19882c397ce41b1c5e"
				},
				success: function(result) {
					console.log(result)
					that.hearlyWeather = result.data.HeWeather6[0].hourly;
				}
			})
		},
		onClick() {
			let that = this
			uni.request({
				url: "https://api.heweather.net/s6/weather/hourly",
				data: {
					location: this.value,
					key: "f6185842beea4a19882c397ce41b1c5e"
				},
				success: function(result) {
					console.log(result)
					that.hearlyWeather = result.data.HeWeather6[0].hourly;
				}
			})
		}
	}
}
